@font-face {
  font-family: 'autocrypt';
  src: url('#{$fonts}ctemplar-fonts/autocrypt.eot?6n2wa6');
  src: url('#{$fonts}ctemplar-fonts/autocrypt.eot?6n2wa6#iefix') format('embedded-opentype'),
    url('#{$fonts}ctemplar-fonts/autocrypt.ttf?6n2wa6') format('truetype'),
    url('#{$fonts}ctemplar-fonts/autocrypt.woff?6n2wa6') format('woff'),
    url('#{$fonts}ctemplar-fonts/autocrypt.svg?6n2wa6#autocrypt') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^='ctemplar-icon-'],
[class*=' ctemplar-icon-'] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'autocrypt' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

