/** 
* ==============================================================================
*     Defining custom variables to be used throughout the project
* ==============================================================================
*/

// == DEFINING PATH REALTED VARIABLES TO ACCESS THE RESOURCES
$assets: '^assets/';
$images: '^assets/images/';
$fonts: '^assets/fonts/';

// == Set font awesome path
$fa-font-path: $fonts;

// == Z-INDEX MASTER LIST

$zindex-neg: -1;
$zindex-zero: 0;
$zindex-default: 1;
$zindex-high: 2;
$zindex-top: 9999;

/** 
* ==============================================================================
*     Overriding bootstrap's varaibles with own brand's variables
* ==============================================================================
*/

//== Colors
//
//## Brand colors for use across Bootstrap.

$brand-primary: #e74c3c;
$brand-secondary: #34495e; //~#091f35;
$brand-success: #3498db;
$brand-info: #f1c40f;
$brand-warning: #f39c12;
$brand-danger: #c0392b; //~#FB0F01;
$brand-white: #ffffff;
$brand-black: #212121;
$brand-gray: rgba(0, 0, 0, 0.54);
$brand-light-gray: rgba(0, 0, 0, 0.38);
$brand-faded: #fafafa;
$brand-blue: #3498db;
$menu-link: rgba(255, 255, 255, 0.7);
$brand-inverse: $brand-black;
$brand-blue: #3a4e63;
$facebook-color: #2d408b;
$twitter-color: #3299ef;
$brand-green: #00e860;
$border-gray: #e8e8e8;
$gray-9e9e9e: #9e9e9e;
$divider-color: #d8d8d8;

//== Scaffolding
//
//## Settings for some of the most global styles.

//** Background color for `<body>`.
$body-bg: $brand-white;
$body-color: rgba(0, 0, 0, 0.54);
//** Global text color on `<body>`.
$text-color: $brand-gray;

//== Typography
//
//## Font, line-height, and color for body text, headings, and more.

$font-family-lato: 'Lato', Helvetica, Arial, sans-serif;
$font-family-roboto: 'Roboto', Helvetica, Arial, sans-serif;
$font-family-base: $font-family-lato;
$font-family-std-base: $font-family-roboto;

// Fonts
//
// Font, line-height, and color for body text, headings, and more.
// $small-font-size:    86.6025%;
$small-font-size: 0.75rem;
$font-size-h1: 3.5rem;
$font-size-h2: 2rem;
$font-size-h3: 1.5rem;
$font-size-h4: 1.25rem;
$font-size-h5: 1.125rem;
$font-size-h6: 1rem;
$font-size-lg: 1rem;

$font-size-sm: 0.75rem;

$headings-line-height: 1.21;
$line-height-base: 1.625;

// $font-size-base:             16px;

//** Unit-less `line-height` for use in components like buttons.
// $line-height-base:        1.25; // 20/14

$font-weight-normal: normal;
$font-weight-bold: bold;
$headings-font-weight: 400;
$headings-font-family: $font-family-base;

//** Headings small color
$headings-small-color: $brand-gray;

//** Global textual link color.
$link-color: $brand-success;
//** Link hover color set via `darken()` function.
$link-hover-color: darken($link-color, 15%);
//** Link hover decoration.
$link-hover-decoration: none;

//** Text muted color
$text-muted: $brand-gray;

// Grid breakpoints
//
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.

$grid-breakpoints: (
  xs: 0,
  sm: 768px,
  md: 1024px,
  lg: 1200px,
  xl: 1440px,
);

// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

$container-xs-max-width: 1170px;

$container-max-widths: (
  xs: $container-xs-max-width,
);

// == Buttons

$btn-padding-y: 0.938rem;
$btn-padding-x: 0.938rem;
$btn-padding-x-sm: 1.5rem; //0.625rem;
$btn-padding-y-sm: 0.5633rem;
$btn-padding-x-lg: 3.43066rem;
$btn-border-radius: 2px;
$btn-font-weight: 600;
$btn-line-height: 1;
$btn-border-radius-lg: 2px;
$btn-border-radius-sm: 2px;

$btn-secondary-bg: $brand-secondary;
$btn-secondary-color: $brand-white;
$btn-secondary-border: $brand-secondary;

// == Dropdowns
$dropdown-min-width: 100%;
$dropdown-margin-top: 0;
$border-radius: 4px;
$dropdown-padding-y: 0;
$dropdown-box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.01), 0 5px 32px 10px rgba(0, 0, 0, 0.05);
$dropdown-item-padding-x: 1.375rem;
$dropdown-link-active-bg: #f7f7f7;
$dropdown-link-active-color: $brand-black;
$dropdown-border-color: #e0e0e0;
$dropdown-link-hover-bg: $brand-faded;

// == Form fields
$form-group-margin-bottom: 1.5rem;
$input-border-color: #e0e0e0;
$input-border-focus: $input-border-color;
$input-border-radius: 4px;
$input-border-radius-sm: 2px;
$input-padding-y-sm: 0.406777rem;
$input-padding-x-sm: 0.938rem;
$input-padding-y: 0.813rem;
$input-padding-x: 1rem;
$input-padding-y-lg: 0.813rem; //~0.938rem;
$input-padding-x-lg: 1rem;
$input-border-radius-lg: 4px;

// == Modal
$modal-header-padding: 1.5rem;
$modal-md: 555px;
$close-color: rgba(0, 0, 0, 0.54);
$modal-header-border-width: 1px;
// $modal-inner-padding: null;
$border-radius-lg: 4px;
$modal-dialog-margin: 0.938rem;

$modal-lg: inherit;
$modal-md: inherit;
$modal-sm: inherit;
$modal-content-border-width: 0;

// == Close
$close-font-size: 2rem;
$close-font-weight: 400;

// == Progressbar
$progress-bar-bg: $brand-danger;

// == Tooltips
$tooltip-bg: #000000;
$tooltip-arrow-color: #000000;

$shadow: 0px 3px 25px 0px rgba(0, 0, 0, 0.25);
$shadow-dark: 0px 3px 25px 0px rgba(0, 0, 0, 0.5);

$spacer: 1rem;
$spacer-x: $spacer;
$spacer-y: $spacer;
$spacers: (
  0: (
    x: 0,
    y: 0,
  ),
  1: (
    x: (
      $spacer-x * 0.25,
    ),
    y: (
      $spacer-y * 0.25,
    ),
  ),
  2: (
    x: (
      $spacer-x * 0.5,
    ),
    y: (
      $spacer-y * 0.5,
    ),
  ),
  3: (
    x: $spacer-x,
    y: $spacer-y,
  ),
  4: (
    x: (
      $spacer-x * 2.15,
    ),
    y: (
      $spacer-y * 2.15,
    ),
  ),
  5: (
    x: (
      $spacer-x * 3,
    ),
    y: (
      $spacer-y * 3,
    ),
  ),
  6: (
    x: (
      $spacer-x * 4,
    ),
    y: (
      $spacer-y * 4,
    ),
  ),
  7: (
    x: (
      $spacer-x * 5,
    ),
    y: (
      $spacer-y * 5,
    ),
  ),
  8: (
    x: (
      $spacer-x * 5.75,
    ),
    y: (
      $spacer-y * 5.75,
    ),
  ),
);
