/**
* @partial: _lists.scss
*/

.lists {
  margin: 0 0;
  padding: 0 0;
  list-style: none;

  > li:not(:last-of-type) {
    margin-bottom: 0.5rem;
  }

  > li > a {
    display: inline-block;
    color: $menu-link;
    vertical-align: top;

    &:hover {
      color: $brand-white;
    }
  }
}

.lists-inline {
  display: flex;
  flex-wrap: wrap;
  line-height: 1;

  > li,
  > .lists-item {
    &:not(:last-of-type) {
      margin-bottom: 0.25rem;
    }
  }

  > li.active,
  > .lists-item.active {
    .lists-link,
    a {
      color: $brand-white !important;
      text-decoration: none !important;
    }
  }
}

.lists-arrow {
  > li > a {
    position: relative;
    padding-left: 1.33rem;

    &::before {
      position: absolute;
      top: 0.5rem;
      left: 0;
      content: '';
      border-style: solid;
      border-width: 0.0938rem 0.0938rem 0 0;
      width: 0.35rem;
      height: 0.35rem;
      transform: rotate(45deg);
    }
  }
}

.lists-dashed {
  margin-right: -13px;
  margin-left: -13px;

  > li {
    position: relative;
    padding: 0 13px;

    &:not(:last-of-type) {
      &::before {
        position: absolute;
        top: 0%;
        right: 0;
        bottom: 0;
        content: '';
        border-right: 1px solid $menu-link;
        width: 1px;
        // transform: translateY(-50%);
      }
    }
  }
}

// == Editor list
.lists-editor {
  margin: 1.5rem 0;
}

.lists-circle-arrow {
  > li {
    position: relative;
    padding-left: 2.125rem;

    &:not(:last-of-type) {
      margin-bottom: 0.875rem;
    }

    &::before {
      position: absolute;
      top: 0.125rem;
      bottom: 0;
      left: 0;
      content: '';
      border: 1px solid #e0e0e0;
      border-radius: 50%;
      height: 1.375rem;
      width: 1.375rem;
    }

    &::after {
      position: absolute;
      top: 0.6rem;
      left: 0.45rem; //~0.5rem;
      content: '';
      border-style: solid;
      border-width: 0.0938rem 0.0938rem 0 0;
      width: 0.35rem;
      height: 0.35rem;
      transform: rotate(45deg);
    }
  }
}
