/**
* Overriding bootstrap modal
*/

.close {
  transition: all ceaser($easeInOutQuint) 0.35s;
}

.modal-sm .close {
  margin-left: 0.25rem;
  font-size: 1.75rem;
}

.modal,
.modal-lg {
  border-radius: 2px;
  // text-align: center;

  .modal-dialog {
    margin-right: auto;
    margin-left: auto;
    width: calc(100% - 30px);
  }
}

.modal-header {
  padding: 0.75rem 0.938rem;
  background: var(--modal-header-background);
  border-radius: 2px;
  line-height: 1;

  @include media('>sm') {
    padding: 0.75rem 1.25rem 0.75rem 1.5rem;
  }

  .modal-lg &,
  .modal-md & {
    @include media('>sm') {
      padding: 1.532rem 1.75rem;
      max-height: 5rem;
    }

    @include media('<=sm') {
      .modal-title {
        font-size: 1rem;
      }
    }
  }
}

.modal-title {
  font-size: 1rem;
  line-height: 1;

  .modal-sm & {
    @include media('>=ipad') {
      font-size: 0.875rem;
    }
  }
}

.modal-body {
  padding: 1.125rem 0.938rem;
  border-bottom-left-radius: $border-radius-lg;
  border-bottom-right-radius: $border-radius-lg;

  @include media('>sm') {
    padding: 1.563rem 1.5rem 1.563rem 1.5rem;
  }
}

.modal-footer {
  padding-right: 1.5rem;
  padding-left: 1.5rem;
  background: var(--modal-header-background);
  border-radius: 2px;
}

.modal-type-default,
.modal-md {
  .modal-body {
    padding: 1.125rem 0.938rem 1.5rem 0.938rem;

    @include media('>sm') {
      padding: 1.875rem 1.563rem 3.125rem 1.563rem;
    }
  }
}

