// == Mailbox modal
.mailbox-modal {
  .modal-dialog {
    max-width: 480px;
    // width: 100%;
  }
}

.modal-lg .modal-dialog {
  max-width: 650px;

  @include media('>sm') {
    .modal-body {
      padding: 1.25rem 2.5rem 1.875rem;
    }
  }

  @include media('>=ipad') {
    .modal-body {
      padding: 1.25rem 4.375rem 1.875rem;
    }
  }
}

.modal-sm .modal-dialog {
  max-width: 550px;

  @include media('<=sm') {
    .modal-body {
      padding: 1.25rem 0.938rem;
    }
  }
}

.modal-md .modal-dialog {
  max-width: 555px;

  @include media('<=sm') {
    .modal-body {
      padding: 1.25rem 0.938rem;
    }
  }
}

.modal-sm .modal-dialog {
  .btn {
    font-size: 0.75rem;
    @include media('<=sm') {
      min-width: inherit;
      padding: 0.5633rem 0.5rem;
    }
  }
}

// == Billing information form
.modal .form-card-holder {
  max-width: inherit;

  .form-step-card-title {
    font-size: 1rem;
  }
}

// == Vertically centered
.modal-dialog-centered {
  display: flex;
  align-items: center;
  pointer-events: none;
  min-height: calc(100% - 1.85rem);

  @include media('>sm') {
    min-height: calc(100% - 3.75rem);
  }

  .modal-content {
    pointer-events: auto;
    width: 100%;
  }
}
